import React, { useState } from 'react';
import { Link } from "gatsby";
import { GatsbyImage, getImage } from "gatsby-plugin-image";
import { graphql, useStaticQuery } from "gatsby";
import styled from "styled-components";
import ModalContact from '../../components/ModalContact';
import bulldozerTexts from '../../shortinfo/bulldozers';
import modelNames from '../../shortinfo/models';
import ModelCard from '../../components/ModelCard';
import Header, { Head as HeaderHead } from '../../components/Header';
import Footer from '../../components/Footer';
import { Layout, Container, Grid, Subtitle } from '../../components/LayoutComponents';//Стилизация компонентов

const bulldozers = [
  "ZD1603", "ZD2203", "ZD220S3", "ZD3203"
];

const BulldozersPage = () => {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [selectedModel, setSelectedModel] = useState(null);
  const [selectedModelText, setSelectedModelText] = useState("");
  
  // Обработчик клика по карточке модели
  const handleCardClick = (model) => {
    setIsModalOpen(true);
    setSelectedModel(model);
    // Используем modelNames для получения удобочитаемого названия
    setSelectedModelText(modelNames[model] || model);
  };
  // Функция закрытия модального окна
  const closeModal = () => {
    setIsModalOpen(false);
    setSelectedModel(null);
    setSelectedModelText("");
  };

  const images = useStaticQuery(graphql`
    query {
      allFile(filter: { relativeDirectory: { eq: "bulldozers" } }) {
        edges {
          node {
            name
            childImageSharp {
              gatsbyImageData(width: 300, layout: CONSTRAINED)
            }
          }
        }
      }
    }
  `);

  // Функция для получения изображения по имени модели
  const getModelImage = (model) => {
    const imageNode = images.allFile.edges.find(edge => edge.node.name === model);
    return imageNode ? getImage(imageNode.node.childImageSharp) : null;
  };

  return (
    <Layout>
      <Header centerText="Бульдозеры" />

      <Container>
        <Grid>
          {bulldozers.map((model) => (
            <ModelCard 
              key={model}
              modelKey={model}
              imageData={getModelImage(model)}
              onClick={() => handleCardClick(model)}
              category="bulldozers"
            />
          ))}
        </Grid>
      </Container>

      {/* Передаём в модальное окно как компонент модель из bulldozerTexts */}
      <ModalContact
        isOpen={isModalOpen}
        onClose={closeModal}
        ModelComponent={bulldozerTexts[selectedModel]}
      >
        {/* Если нужно, можно добавить выбранный текст прямо сюда */}
        {selectedModelText && <Subtitle>{selectedModelText}</Subtitle>}
      </ModalContact>

      <Footer
        companyName="Зумлион Индустри"
        yearsActive="25"
        websiteUrl="https://example.com"
      />
    </Layout>
  );
};

export default BulldozersPage;

export const Head = HeaderHead;